<template>
  <div v-if="student">
    <v-row>
      <v-col md="4" cols="12">
        <v-card raised shaped>
          <v-card-title>
            <v-row class="text-left">
              <v-col cols="12">
                <v-icon class="mx-3">mdi-account</v-icon>
                <span class="font-weight-light">Name:</span>
                <strong>
                  {{ student.firstName }} {{ student.middleName }}
                  {{ student.lastName }}</strong
                >
              </v-col>
              <v-col cols="12">
                <v-icon class="mx-3">mdi-cake-variant</v-icon>
                <span class="font-weight-light">Birthday:</span>
                <strong>
                  {{ student.dateOfBirth }}
                </strong>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
      <v-col md="4" cols="12">
        <v-card raised shaped>
          <v-card-title>
            <v-row class="text-left">
              <v-col cols="12">
                <v-icon class="mx-3">mdi-book</v-icon>
                <span class="font-weight-light">Homework Ratio:</span>
                <strong>
                  {{ answeredCount }} of
                  {{ assignments.length }}
                </strong>
              </v-col>
              <v-col cols="12">
                <v-icon class="mx-3">mdi-calculator</v-icon>
                <span class="font-weight-light">Average:</span>
                <strong
                  :class="{
                    'green--text': average > 50,
                    'red--text': average < 30,
                    'amber--text': average > 30 && average < 50
                  }"
                >
                  {{ average }} %
                </strong>
              </v-col>
            </v-row>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col md="4" cols="12">
        <v-card raised outlined>
          <v-card-title class="text-center">
            <h2>Homeworks</h2>
          </v-card-title>
          <v-card-text>
            <v-select
              label="Enrolled"
              outlined
              :item-text="item => selectEnrolledText(item)"
              item-value="index"
              v-model="enrollForHomeworks"
              :items="enrolleds"
            >
            </v-select>
            <chartist
              class="ct-chart"
              ratio="ct-major-second"
              type="Line"
              :data="assignmentChartData"
              :options="chartOptions"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col md="4" cols="12">
        <v-card raised outlined>
          <v-card-title class="text-center">
            <h2>Marks</h2>
          </v-card-title>
          <v-card-text>
            <v-select
              label="Enrolled"
              outlined
              :item-text="item => selectEnrolledText(item)"
              item-value="index"
              v-model="enrollForMarks"
              :items="enrolleds"
            >
            </v-select>
            <chartist
              class="ct-chart"
              ratio="ct-major-second"
              type="Bar"
              :data="markChartData"
              :options="{
                high: 100,
                low: 0
              }"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col md="4" cols="12" style="height: 100%">
        <v-card raised outlined>
          <v-card-title class="text-center">
            <h2>Enrollments</h2>
          </v-card-title>
          <v-card-text>
            <chartist
              class="ct-chart"
              ratio="ct-major-second"
              type="Bar"
              :data="enrolledsChartData"
              :options="{
                high: 100,
                low: 0
              }"
            >
            </chartist>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <!-- <v-row>
      <v-col
        cols="12"
        lg="3"
        md="4"
        sm="6"
        v-for="(enrolled, index) in enrolleds"
        :key="index"
      >
        <v-card raised shaped dark>
          <v-row class="ml-3 text-left">
            <v-col cols="12">
              <v-icon class="mr-3">mdi-book</v-icon>
              {{ enrolled }}
            </v-col>
            <v-col cols="12">
              <v-icon class="mr-3">mdi-account</v-icon>
              {{ enrolled.teacher.firstName }}
            </v-col>
            <v-col cols="12">
              <v-icon class="mr-3">mdi-calculator</v-icon>
              {{ enrolled.average }} %
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row> -->
  </div>
</template>

<script>
import StudentService from "../services/StudentService";

export default {
  data() {
    return {
      enrolleds: [],
      assignments: [],
      assignmentChartData: {},
      markChartData: {},
      enrolledsChartData: {},
      chartOptions: {
        lineSmooth: false,
        low: 0,
        high: 100,
        autoScaleAxis: true
      },
      enrollForHomeworks: Number,
      enrollForMarks: Number,
      studentId: Number,
      student: Object,
      average: Number,
      ranking: Number,
      assignmentMarks: [],
      axisX: {},
      answeredCount: 0
    };
  },

  created() {
    this.studentId = this.$route.params.studentId;
    this.ranking = this.$route.params.ranking;
    StudentService.GetStudent(this.studentId).then(student => {
      this.student = student;
      StudentService.GetEnrolleds(this.studentId).then(enrolleds => {
        this.enrolleds = enrolleds;
        if (enrolleds.length > 0) {
          this.enrollForHomeworks = 0;
          this.enrollForMarks = 0;

          let sum = 0;

          let labels = [];
          let series = [[], []];
          enrolleds.forEach(element => {
            sum += parseFloat(element.result);

            let name = "";
            name = name.concat(
              element.Offer.Course.name,
              " ",
              element.Offer.code,
              " ",
              element.Offer.Teacher.firstName +
                " " +
                element.Offer.Teacher.lastName
            );
            labels.push(name);
            series[0].push(element.result);
            series[1].push(element.Offer.average);
            this.enrolledsChartData = { labels, series };
          });
          this.average = (sum / enrolleds.length).toFixed(2);
        }
      });
      StudentService.GetStudentAssignments(this.studentId).then(assignments => {
        this.assignments = assignments;
        assignments.forEach(assignment => {
          if (assignment.isAnswered) {
            this.answeredCount++;
          }
        });
      });
    });
  },

  methods: {
    selectEnrolledText(item) {
      let string = "";

      string = string.concat(
        item.Offer.Course.name,
        " ",
        item.Offer.code,
        " ",
        item.Offer.Teacher.firstName + " " + item.Offer.Teacher.lastName
      );

      return string;
    }
  },

  watch: {
    enrollForHomeworks: function() {
      let labels = [];
      let series = [[], []];

      this.enrolleds[this.enrollForHomeworks].Assignments.forEach(element => {
        series[0].push(element.mark);
        series[1].push(element.Homework.average);
        labels.push(element.Homework.name);
      });

      this.assignmentChartData = { labels, series };
    },

    enrollForMarks: function() {
      let labels = [];
      let series = [[]];

      this.enrolleds[this.enrollForMarks].marks.forEach(element => {
        series[0].push(element.points);
        labels.push(element.name);
      });

      this.markChartData = { labels, series };
    },

    enrolleds: function() {
      for (let i = 0; i < this.enrolleds.length; i++) {
        this.$set(this.enrolleds[i], "index", i);
      }
    }
  }
};
</script>

<style>
.ct-label {
  color: #757575;
}

.ct-series-a .ct-line,
.ct-series-a .ct-point {
  stroke: #2196f3;
}

.ct-series-b .ct-line,
.ct-series-b .ct-point {
  stroke: #f44336;
}

.ct-series-a .ct-bar {
  stroke: #2196f3;
  stroke-width: 20px;
}

.ct-series-b .ct-bar {
  stroke: #f44336;
  stroke-width: 20px;
}
</style>